import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NumberService } from './../../shared/services/number.service';
import {MatDialog} from '@angular/material';


@Component({
  selector: 'app-new-number',
  templateUrl: './new-number.component.html',
  styleUrls: ['./new-number.component.scss']
})
export class NewNumberComponent implements OnInit {
  @Output()
  getLatestNumbers: EventEmitter<any> = new EventEmitter<any>();
  constructor(
    private NumberService: NumberService,
    public dialog: MatDialog
  ) { }
  public twillioAreaNumbers:any = [];
  public selectednumber:any;
  public areacode:string ="";
  public cardLengthBig:Boolean = false;
  public getTwillioAreaNumbersLoaderVisualization:any;
  public addTwillioAreaNumbersLoaderVisualization:Boolean =false;

  public processing: Boolean = false;
  ngOnInit() {
  }

  

  getAreaNumbers (code) {
      this.getTwillioAreaNumbersLoaderVisualization = true;
      this.NumberService.getAreaNumbers(code).subscribe((response: any ) => {
          this.cardLengthBig =true
          this.twillioAreaNumbers = response
          console.log(response);
          this.getTwillioAreaNumbersLoaderVisualization = false;
      }, (response: any) => {
        let error;
        this.getTwillioAreaNumbersLoaderVisualization = false;
        if (response.error.error.message){
            error = response.error.error.message;
        } else {
            error = response.error.error;
        }
      });
  };

  cancelAdd () {
    this.twillioAreaNumbers = [];
    this.cardLengthBig = false;
    this.areacode= ""
  }




  addNumber() {
    this.addTwillioAreaNumbersLoaderVisualization = true;
    this.NumberService.addNumbers(this.selectednumber).subscribe((response: any ) => {
        console.log(response);
        this.getLatestNumbers.emit(response);
        this.addTwillioAreaNumbersLoaderVisualization = false;
    }, (response: any) => {
      let error;
      this.addTwillioAreaNumbersLoaderVisualization = false;
      if (response.error.error.message){
          error = response.error.error.message;
      } else {
          error = response.error.error;
      }
    })
  }

}






