import { Component, OnInit } from '@angular/core';
import { NumberService } from './../shared/services/number.service';

@Component({
  selector: 'app-numbers',
  templateUrl: './numbers.component.html',
  styleUrls: ['./numbers.component.scss']
})
export class NumbersComponent implements OnInit {

  public searchKeyword:any = '';
  public numberList:any;
  public twillioAreaNumbers:any;
  public getTwillioAreaNumbersLoaderVisualization:any;
  public numberLoaderVisualization: Boolean = true;
  constructor(
    private NumberService: NumberService
  ) { }

  ngOnInit() {
    this.getNumbers()

  }
  getNumbers(){
    this.NumberService.getNumbers(this.searchKeyword).subscribe((response: any) => {
        if (response.error== null) {
            this.numberLoaderVisualization = false;
            this.numberList = response.results;
            console.log('contacts :', response.results);
        };
        this.numberLoaderVisualization = false;
    });
  };

  /*================= for emitter================*/
  
  getLatestNumbers(){
    this.numberLoaderVisualization = true;
    this.NumberService.getNumbers(this.searchKeyword).subscribe((response: any) => {
        if (response.error== null) {
            this.numberLoaderVisualization = false;
            this.numberList = response.results;
            console.log('contacts :', response.results);
        };
        this.numberLoaderVisualization = false;
    });
  };




  

}



