import { Component, OnInit, ElementRef, ViewChild, Output, EventEmitter, Input } from '@angular/core';
import { WindowRef } from '../shared/services/window-ref.service';
import { UserService } from '../shared/services/user.service';
import { ApiService } from '../shared/services/api.service';

@Component({
  selector: 'app-stripe',
  templateUrl: './stripe.component.html',
  styleUrls: ['./stripe.component.scss']
})
export class StripeComponent implements OnInit {


  stripe: any
  edit: boolean = false;
  card: any
  email: any
  loader: boolean = false;
  stripeData :any;
  planlist : any;
  enabledSubscription: any = [];
  unsubscribedList : any = [];

  constructor(
    private UserService: UserService,
    private winRef: WindowRef,
    private apiService: ApiService
  ) { }

  ngOnInit() {
    if(this.UserService.user['stripeId'] != null){
      this.retrieveCardDetail();
    }else{
      this.stripeData = null;
  }
    this.stripe = this.winRef.nativeWindow.Stripe('pk_test_wdevjExYH28dYzFg05q7GQTW');
    this.subscribePlan();
    this.listPlans();
  }


  complete = this.UserService.completed_functions.stripe_completed


  @Output() completed = new EventEmitter();
  @Input() disableEdit: any;
  
  ngAfterViewInit() {

  }

  initCardForm() {

  }

  retrieveCardDetail(){
    return this.apiService.call('/api/stripe/retrieveCustomer').subscribe((response: any) => { 
      this.stripeData = response.message;
      
               }, (error) => {   
                console.log(error)
                 })    
  }

  listPlans(){
    var found = false;
    this.unsubscribedList = []
    return this.apiService.call('/api/stripe/listPlan').subscribe((response: any) => { 
      this.unsubscribedList = response.message;

      //if needed for only one select

      // console.log('----->plan',response.message)
      // response.message.forEach(plan => {
      //   if(this.enabledSubscription.length == 0){
      //     this.unsubscribedList = response.message;
      //   }else{
      //     this.enabledSubscription.forEach(subsription  => {
      //       if(subsription.planName == plan.name){
      //         found = true;
      //       }else{
      //         found = false
      //       }
  
      //     });
      //     if(!found){
      //       this.unsubscribedList.push(plan)
      //     }
      //   }
      // })
               }, (error) => {   
                console.log(error)
                 }) 
  }

  cancelSubscription(subscribeId){
    var postData = {
      subscriptionId:subscribeId
    }
    return this.apiService.call('/api/stripe/cancelSubscription',postData).subscribe((response: any) => { 
      this.subscribePlan();
    }, (error) => {   
                console.log(error)
                 }) 
  }

  subscribePlan(){
    return this.apiService.call('/api/stripe/enabledSubscription').subscribe((response: any) => { 
      this.enabledSubscription = response.message;
      console.log('----->enabledSubscription',this.enabledSubscription)
               }, (error) => {   
                console.log(error)
                 }) 
  }

  subsription(value){
    console.log(value)
    var postData = {
      plainId : value
    }
    return this.apiService.call('/api/stripe/joinSubscription',postData).subscribe((response: any) => { 
      console.log('data',response)
      this.subscribePlan();
      // this.planlist = response.message;
               }, (error) => {   
                console.log(error)
                 }) 
  }

 

  submitCreditCard(email, callback) {
    this.stripe.createSource(this.card, {
      owner: {
        name: this.UserService.user.firm,
        email: email
      },
      metadata: localStorage.getItem('user')['id']
    }).then(function (result) {
        callback(result);
    });
  };

  

  save($event) {
    console.log('save')
    console.log($event)
    let email = this.email = $event.email;
    this.card = $event.card;
    this.loader = true;
    this.submitCreditCard(email, stripeResult => {
      if (stripeResult.error) {
        alert('Stripe Error')
      } else {
        let postData = {
          client_secret:stripeResult.source.client_secret,
          id:stripeResult.source.id,
          email:this.email
        }
        if(!this.UserService.user['stripeId']){
          return this.apiService.call('/api/stripe/createCustomer', postData).subscribe((response: any) => { 
            this.edit = false;
            this.loader = false;
            this.completed.emit('stripeId'); 
            return this.apiService.call('/api/refreshToken', {email:this.UserService.user['email']}).subscribe((response: any) => { 
              this.UserService.start(response);
              this.ngOnInit()
                       }, (error) => {   
                         console.log(error)
                         })
                     }, (error) => {   
                      alert("Error Sending");
                       })
        }else{
          return this.apiService.call('/api/stripe/updateCustomer', postData).subscribe((response: any) => { 
            this.edit = false;
            this.loader = false;
            this.completed.emit('stripeId'); 
            return this.apiService.call('/api/refreshToken', {email:this.UserService.user['email']}).subscribe((response: any) => { 
              console.log('refresh token',response)
              this.UserService.start(response);
                       }, (error) => {   
                         console.log(error)
                         })
                     }, (error) => {   
                      alert("Error Sending");
                       })
        }
      };
    });
  }

  

}



