import { Component, OnInit, Input, Inject, Output, EventEmitter } from '@angular/core';
import { NumberService } from './../../shared/services/number.service';
import {MatDialog} from '@angular/material';
import {DeleteNumberWarningModalComponent} from './../delete-number-warning-modal/delete-number-warning-modal.component';
import {VerificationCallWarningModalComponent} from './../verification-call-warning-modal/verification-call-warning-modal.component';
import { ConfirmationModalComponent } from './../confirmation-modal/confirmation-modal.component'
@Component({
  selector: 'app-numberlist-card',
  templateUrl: './numberlist-card.component.html',
  styleUrls: ['./numberlist-card.component.scss']
})
export class NumberlistCardComponent implements OnInit {
  @Output()
  getLatestNumbers: EventEmitter<any> = new EventEmitter<any>();
  public code: any
  @Input('number') number;
  constructor(
    private NumberService: NumberService,
    public dialog: MatDialog
    
  ) { }

  ngOnInit() {

  }

  openDeleteWarningModal (number, sid) {
    let dialogRef = this.dialog.open(DeleteNumberWarningModalComponent, {
      width: '320px',
      data: {number, sid},
    });

    dialogRef.afterClosed().subscribe(result => {
        if(result == 'new') {
            this.getLatestNumbers.emit();
        };
    });
  };

  callToVerify (number, sid, extension) {
    let dialogRef = this.dialog.open(VerificationCallWarningModalComponent, {
      width: '320px',
      data: {number, sid, extension},
    });

    dialogRef.afterClosed().subscribe(response => {
        if(response) {
            this.number.status = response.body.status
            this.code = response.body.verification_code;
        };
    });

    dialogRef.afterClosed().subscribe(response => {
      if(response == 'new') {
        this.getLatestNumbers.emit();
      };
      
    });
  };


  createLoaDocument (sid) {
      this.NumberService.createLoaDocument(sid).subscribe((response: any ) => {
          console.log(response.result.body.message);
          let res = response.result.body.message;
          let dialogRef = this.dialog.open(ConfirmationModalComponent, {
            width: '320px',
            data: {res},
          });

      }, (response: any) => {
        let error;
        if (response.error.error.message){
            error = response.error.error.message;
        } else {
            error = response.error.error;
        }
      });
  }


  submitVerificationCode (code) {
      this.NumberService.submitVerificationCode(code, this.number.sid).subscribe((response: any ) => {
          console.log(response);
      }, (response: any) => {
        let error;
        if (response.error.error.message){
            error = response.error.error.message;
        } else {
            error = response.error.error;
        }
      });
  };
  

  

}





