import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NumberService } from './../../shared/services/number.service';
import { UserService } from './../../shared/services/user.service';
import {MatDialog} from '@angular/material';

@Component({
  selector: 'app-hosted-number',
  templateUrl: './hosted-number.component.html',
  styleUrls: ['./hosted-number.component.scss']
})
export class HostedNumberComponent implements OnInit {
  @Output()
  getLatestNumbers: EventEmitter<any> = new EventEmitter<any>();
  constructor(
    private NumberService: NumberService,
    private UserService: UserService,
    public dialog: MatDialog
  ) { }
  public twillioAreaNumbers:any = [];
  public selectednumber:any;
  public address:any = {};
  public areacode:string ="";
  public user:any = this.UserService.user;
  public hostedNumberModel:any = {};
  public cardLengthBig:Boolean = false;
  public newAddressFrom:Boolean = false;
  public useEmailQuestion:Boolean = false;
  public createHostedNumberForm:Boolean = false;
  public hostedNumber:Boolean = false;
  public getTwillioAreaNumbersLoaderVisualization:any;
  public addTwillioAreaNumbersLoaderVisualization:Boolean =false;
  public processing: Boolean = false;

  ngOnInit() {
    
  }

  createAddress () {
    this.NumberService.addAddress(this.address).subscribe((response: any ) => {
      console.log(response);
      this.newAddressFrom = false;
      this.useEmailQuestion = true;
      this.hostedNumberModel.accountAddressSid = response.result.body.sid;
    }, (response: any) => {
      let error;
      this.newAddressFrom = false;
      this.useEmailQuestion = true;
      this.getTwillioAreaNumbersLoaderVisualization = false;
      if (response.error){
        console.log('createAddress:',response);
      }
    });
  }


  createHostedNumber () {
      this.NumberService.addHostedNumber(this.hostedNumberModel).subscribe((response: any) => {
          console.log(response);
          this.getLatestNumbers.emit(response);
      }, (response: any) => {
          let error;
          this.getTwillioAreaNumbersLoaderVisualization = false;
          if (response.error){
            console.log('createAddress:',response);
          }
      });
  }
  






 

}



