import { Component, OnInit,ElementRef, ViewChild, Output, EventEmitter, Input,AfterViewInit } from '@angular/core';
import { UserService } from '../../shared/services/user.service';


@Component({
  selector: 'app-stripe-form',
  templateUrl: './stripe-form.component.html',
  styleUrls: ['./stripe-form.component.scss'],
  providers: []
})
export class StripeFormComponent implements OnInit {

  constructor(
    private UserService: UserService,
  ) { }

  @Output() onSubmit = new EventEmitter();
  @Output() onCancel = new EventEmitter();
  @Input() complete: any
  elements: any
  card: any
  @Input() processing: boolean;
  @Input() stripe: any;
  @Input() stripeCardInfo : any;

  ngOnInit() {
    this.elements = this.stripe.elements();
    this.card = this.elements.create('card');
    this.el.nativeElement.id = "cardelement";
    this.card.mount('#cardelement');
    if(this.UserService.user['stripeId'] != null){
      this.getChecked();

    }
      }

  ngAfterViewChecked (){
    
  }

  getChecked() {
    console.log('tetsw',this.stripe)
    document.getElementById('emailInput')['value'] = this.stripeCardInfo.email;
  }

  check(){
  }

  @ViewChild('cardelement') el: ElementRef;

  ngAfterViewInit() {

  }

  shouldDisableSubmit() {
    if (this.processing === true) {
      return true;
    }
    return false;
  }

  getSubmitBtnLabel() {
    if (this.processing === true) {
      return 'please wait...';
    }

    return 'Save';
  }

  save(email) {
    // console.log("test",this.card)
    this.onSubmit.emit({ card: this.card, email: email });
  }

}



